import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const App = r => require.ensure([], () => r(require('../App')), 'app')

const Browser = r => require.ensure([], () => r(require('../components/browser')), 'browser')

const testregister = r => require.ensure([], () => r(require('../components/testregister')), 'testregister')
const testissueandtransaction = r => require.ensure([], () => r(require('../components/testissueandtransaction')), 'testissueandtransaction')
const testtransaction = r => require.ensure([], () => r(require('../components/testtransaction')), 'testtransaction')
const testtransactionandcancel = r => require.ensure([], () => r(require('../components/testtransactionandcancel')), 'testtransactionandcancel')


const routes = [
  {
    path: '/',
    component: App,
    children: [
      {
        path: '',
        redirect: '/browser'
      },
      /*{
        path: '/degreeCz/:txId/:type',
        component: DegreeCz
      }*/
      {
        path: '/browser',
        component: Browser
      },

      {
        path: '/testregister',
        component:testregister
      },
      {
        path: '/testissueandtransaction',
        component:testissueandtransaction
      },
      {
        path: '/testtransaction',
        component:testtransaction
      },
      {
        path: '/testtransactionandcancel',
        component:testtransactionandcancel
      }
    ]
  },
  { path: '*', redirect: '/' }
];

const router = new Router({
  mode: 'hash',
  scrollBehavior: () => ({ y: 0 }),
  routes
})

/*  router.beforeEach((to, from, next) => {
  alert(to)
  console.log(from)
  next()
})  */
export default router
/* export default new Router({
  mode: 'hash',
  scrollBehavior: () => ({ y: 0 }),
  routes
}) */




